export const consoleLogger = {
    debug(msg) {
        console.log(msg);
    },
    warn(msg) {
        console.warn(msg);
    }
};
export const silentLogger = {
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    debug(msg) { },
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    warn(msg) { }
};
