import * as yml from 'yaml';
export function create404Wrapper() {
    return () => create404Fm();
}
class NotFoundFm {
    constructor() {
        this.layout = 'default';
        this.title = '404';
        this.nav_exclude = true;
        this.search_exclude = true;
    }
}
function create404Fm() {
    return {
        type: 'yaml',
        value: yml.stringify(new NotFoundFm())
    };
}
